import Layout from '../components/Layout'
import fetch from 'isomorphic-unfetch';

const Albums = ({ albums }) => (
  <>
    <Layout>
      <div className="row">
        {albums ? albums.map(album => {
          (
            <>
              <p className={`${album.classes.col.txt}`}>{album.title}</p>
              <div className={`${album.classes.col.img}`}>
                <img
                  className="hero-image"
                  src={`//localhost:3333/${album.photos[0].path}`}
                  alt={album.content}
                  width="100%"
                />
              </div>
            </>
          );
        }) : null
        };

      </div>
    </Layout>
  </>

)

Albums.getInitialProps = async function (context) {

  const res = await fetch(`http://localhost:3333/albums?_embed=photos`);
  const albums = await res.json();

  return { albums };
};


export default Albums;